@charset "UTF-8";
@import "bourbon/bourbon";
@import "../base/grid-settings";
@import "color";
@import "../base/variables";
@import "neat/neat";

body {
background: #7db9e8; /* Old browsers */
background: -moz-linear-gradient(top,  #7db9e8 0%, #2989d8 50%, #1e5799 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top,  #7db9e8 0%,#2989d8 50%,#1e5799 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  #7db9e8 0%,#2989d8 50%,#1e5799 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7db9e8', endColorstr='#1e5799',GradientType=0 ); /* IE6-9 */
}
body.boxed #page {
	max-width: 1120px !important;
}
#content a:hover {
	text-decoration: underline;
}
.snappy-row {
	padding-left: 0 !important;
	padding-right: 0 !important;
}
.site-branding > .container {
	@include media($medium-screen) {
		height: 140px;
	}
}
#snappy-mobile-menu {
	position: absolute;
	z-index: 9999;
	right: 10px;
}
#snappy-mobile-search {
	position: absolute;
	z-index: 9999;
	right: 50px;
}

.snappy-menu-wrapper, .gradient {
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#6db3f2+0,54a3ee+50,3690f0+51,1e69de+100;Blue+Gloss+%233 */
background: #6db3f2; /* Old browsers */
background: -moz-linear-gradient(top,  #6db3f2 0%, #54a3ee 50%, #3690f0 51%, #1e69de 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top,  #6db3f2 0%,#54a3ee 50%,#3690f0 51%,#1e69de 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  #6db3f2 0%,#54a3ee 50%,#3690f0 51%,#1e69de 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6db3f2', endColorstr='#1e69de',GradientType=0 ); /* IE6-9 */

}
#primary {
	padding: 0 10px;
	font-size: 0.85em;
}
.page, .single {
	#primary {
		border: 1px solid #ddd;
	}
}
.home #primary {
	border: none;
}
#content {
	.container {
		body:not(.home) & {
			padding-top: 5px;
		}
		#breadcrumbs {
			margin-bottom: 5px;
			padding-left: 10px;
		}
	}
}
.widget-area {
	.widget-title {
		@extend .gradient;
		line-height: 17px;
	}

	aside {
		border: none;
	}
	
	.widget_nav_menu {
		li {
			border-bottom: 1px dashed #ddd;
			padding: 3px 5px;
			&:before {
				content: 'Â»';
				margin-right: 5px;
			}
			a {
				font-weight: bold;
				color: color(black);
				&:hover {
					color: color(primary, dark);
				}
			}	
		}
	}

}

#colophon {
	#menu-footer-menu {
		@extend .snappy-menu-wrapper;
		text-align: center;
		li {
			display: inline-block;
			padding: 5px 10px;
		}
		a {
			font-weight: bold;
			color: color(white);
		}
	}
	.textwidget {
		text-align: center;
		padding: 10px;
		p {
			margin-bottom: 0;
		}
		.site-name {
			font-weight: bold;
			text-transform: uppercase;
		}
	}
}
.single {
	main article {
		header {
			img.attachment-thumbnail {
			    width: 100px;
			    float: left;
			    margin-right: 10px;
			    border-radius: 3px;
			}

		}

		.entry-title {
			display: inline;
		}
		.entry-excerpt {
			font-style: italic;
			font-weight: bold;
			color: color(gray);
		}
	}
}
.toparticle {
	margin-right: -10px;
	margin-left: -10px;
	padding: 3px 10px;
	color: #fff;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 12px;
	margin-bottom: 7px;
	line-height: 20px;
}

.archive {
	article {
		@include clearfix;
		border-bottom: 1px dashed #ddd;
		padding-bottom: 10px;
		margin-bottom: 7px;
	}
	.item-title {
		font-weight: bold;
		font-size: 14px;
		display: block;
	}
	.item-thumbnail {
		width: 150px;
		display: block;
		float: left;
		margin-right: 10px;
	}
}

.cat-block {
	border: 1px solid #ddd;
	padding: 0 10px;
	margin-left: -10px;
	margin-right: -10px;
	margin-bottom: 10px;

	h2 a {
		color: color(white);
	}
	.item-title {
		font-weight: bold;
		font-size: 14px;
		display: block;
	}
	.item-thumbnail {
		width: 150px;
		display: block;
		float: left;
		margin-right: 10px;
	}
	.offset-area .post {
		@include clearfix;
		border-bottom: 1px dashed #ddd;
		margin-bottom: 0px;
		padding-bottom: 10px;
	}
	ul {
		list-style: disc;
		padding-left: 1.3em;
		.item-title {
			font-size: 0.9em;
			color: color(black);
			border-bottom: 1px dashed #eee;
			&:hover {
				color: color(primary);
			}
		}
		li:last-child a {
			border-bottom: none;
		}
	}
}
.widget-area aside.widget_sp_image {
	padding: 0 8px 8px;
}
#text-3 {
	ul {
		list-style: square;
		padding-left: 20px;
	}
	a {
		color: #333;
	}
}

nav#site-navigation ul.sub-menu {
	@include media($medium-screen) {
		border: 1px solid color(gray);
	}
}
.posted-on {
	font-weight: 400;
	font-style: italic;
	font-size: 12px;
	color: color(gray);

	&.new-post {
		background: url(img/new1.gif) no-repeat right top;
		padding-right: 30px; 
		.normal-area & {
			background-image: url(img/new1.png);
		}
	}
}

#respond {
	padding: 0 10px 10px;
	background: #f5f5f5;
	margin-bottom: 10px;

	#reply-title {
		@extend .gradient;
		color: #fff;
		padding: 5px 10px;
		margin-right: -10px;
		margin-left: -10px;
	}
}
.rp4wp-related-posts {
	h3 {
		@extend .gradient;
		color: #fff;
		padding: 5px 10px;
		margin-bottom: 0;
	}
	ul {
		padding: 7px !important;
		@include clearfix;
		background: #f5f5f5;
		
		> li {
			@include clearfix;
			padding-bottom: 10px !important;
			margin-bottom: 10px !important;
			border-bottom: 1px dashed #ccc;
		}

		.rp4wp-related-post-image {
			width: auto;
			padding-right: 10px;
		}
		
		img {
			width: 75px;
			height: auto;
			border-radius: 3px;
		}
	}
}
.tin-noi-bat {
	.snappy-loop {
		margin-left: -10px;
		margin-right: -10px;	
		background: #f5f5f5;
	}
	margin-bottom: 20px;
	h2 {
		margin-bottom: 0;
		color: yellow;
		font-size: 16px;
	}
	.item-excerpt {
		padding: 10px;
		margin-bottom: 0;
		color: #fff;
	}
	.item-title {
		color: #fff;
		font-weight: bold;
		font-size:  16px;
		padding: 10px 10px 0;
		display: block;
	}
	.main-slide {
		.slick-slide {
			position: relative;
		}
		.info {
			position: absolute;
			bottom: 0;
			width: 100%;
			background: rgba(#000, 0.6);
		}
		.slick-dots li {
			display: inline-block;
		}
		img {
			width: 600px;
			height: 400px;
			max-width: 100%;
		}
	}
	.slide-thumb {
		.slick-slide {
			padding: 4px;
		}
	}
}
.videoplayer embed {
	max-width: 100%;
}
.video-clip {
	margin-bottom: 10px;
	.toparticle {
		margin-bottom: 0;
	}
}
.videos {
	margin-right: -10px;
	margin-left: -10px;
	.small-videos {
		@include clearfix;
		.item {
			cursor: pointer;
			@include clearfix;
			@include linear-gradient(#fff, #eee);
			.home & {
				@include media($large-screen) {
					width: 50%;
					float: left;
				}
			}
			&:hover {
				@include linear-gradient(#fff, #ddd);
			}
		}
		span {
			vertical-align: top;
			font-weight: bold;
			font-size: 12px;
		}
		img {
			width: 60px;
			height: 60px;
			float: left;
			margin-right: 5px;
		}
	}
	iframe {
		width: 100%;
		vertical-align: top;
	}
}
.cai-cach-hanh-chinh {
	li {
		margin-bottom: 10px;
	}
	ul {
		.item-title {
			font-weight: 400;
			border-bottom: none;
		}
	}
	.counter {
		color: #111;

		span {
			font-weight: 700;
		}

		&:hover {
			text-decoration: none !important;
		}
	}
}

.table.thu-tuc {
	tr td:first-child {
		font-weight: bold;
	}
	td {
		vertical-align: top;
		border: 1px solid #ddd;
		padding: 5px;
	}
}
form#commentform p {
    margin-bottom: 0;
}
.table-content {
	margin-right: -10px;
	margin-left: -10px;
}
.table.van-ban {
	tr {
		td, th {
			width: 25%;
			border: 1px solid #ddd;
			padding: 5px;
			text-align: center;

			&:last-child {
				width: 50%;
				text-align: left;
				line-height: 1.2;
			}
		}
		th {
			background: #eee;
			text-align: center !important;
		}
	}
}